.ecotope.contain {
  padding: .9rem .1rem;
  display: flex;
}
.ecotope.contain .cont-box {
  width: 25vw;
}
.ecotope.contain .cont-left {
  flex: 3;
}
.ecotope.contain .cont-left .sanitation ul {
  position: relative;
  z-index: 2;
  padding: 0 .5rem;
  margin-top: .2rem;
  box-sizing: border-box;
}
.ecotope.contain .cont-left .sanitation ul li {
  float: left;
  position: relative;
  margin-right: 8vw;
  text-align: center;
}
.ecotope.contain .cont-left .sanitation ul li::after {
  position: absolute;
  content: "";
  z-index: 1;
  bottom: .3rem;
  left: 0rem;
  width: 1.25rem;
  height: .25rem;
  border-radius: 50%;
  background: #004986;
  display: none;
}
.ecotope.contain .cont-left .sanitation ul li a {
  font-size: .16rem;
  color: #dcfdff;
  font-weight: bold;
}
.ecotope.contain .cont-left .sanitation ul li i {
  display: block;
  position: relative;
  margin-left: .05rem;
  z-index: 2;
}
.ecotope.contain .cont-left .sanitation ul li span {
  position: absolute;
  z-index: 3;
  top: 0;
  right: 0;
  display: block;
  width: .3rem;
  height: .3rem;
  line-height: .3rem;
  border-radius: 50%;
  background: #ba681e;
  color: #fff;
}
.ecotope.contain .cont-left .sanitation ul li:nth-child(even) {
  margin-right: 0;
}
.ecotope.contain .cont-left .sanitation ul li:nth-child(3) i,
.ecotope.contain .cont-left .sanitation ul li:nth-child(4) i {
  margin-left: .1rem;
}
.ecotope.contain .cont-left .sanitation ul li:nth-child(3) span,
.ecotope.contain .cont-left .sanitation ul li:nth-child(4) span {
  top: .1rem;
  right: .1rem;
}
.ecotope.contain .cont-left .sanitation ul li:hover:after,
.ecotope.contain .cont-left .sanitation ul li.active:after {
  display: block;
}
.ecotope.contain .cont-right {
  flex: 3;
}
.ecotope.contain .cont-center {
  flex: 6;
}
.ecotope.contain .cont-center .cont-list ul li {
  width: 2.2rem;
  height: 1.3rem;
  color: #4df1bd;
}
.ecotope.contain .cont-center .cont-list ul li:last-child {
  color: #ffc90d;
}
